@use 'reset';

$violet : #7469F0;

@mixin flexRow {
    display :flex;
    flex-direction: row;
}
@mixin flexColumn {
    display :flex;
    flex-direction: column;
}

// 상단 베스트 상품 나열해주는 믹신
@mixin bestProductList {
    margin: 70px auto;
    @include flexRow();
    justify-content: center;
    justify-content: space-around
}

// 6개 상품 나열해주는 믹신
@mixin productList {
    @include flexRow();
    justify-content: center;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-basis: 250px;
}


.wrap {
    width: 1280px;
    margin: 20px auto;
}

.login {
    width: 1280px;
    @include flexRow();
    justify-content: flex-end;
    // 로그인 / 회원가입 우측 상단으로 보내려고함
}

.login > a {    
    margin-right: 10px;
}

.login > a:hover {
    color:$violet;
}

.logo {
    margin: 15px 0 0 25px;
    width: 150px;
    display: inline-block;
}

.logo > h1 {
    width: 150px;
    font-size: 30px;
    color: $violet;
    font-weight: bold;
}

// 메뉴
.nav {
    margin: 15px 0 35px 110px;
    width: 800px;
    height: 25px;
    display: inline-block;
}

.nav > ul {
    width: 800px;
    @include flexRow();
    text-align: center;
    font-size: 20px;
}

.nav > ul > li {
    width: 180px;
    margin-left: 50px;
}

.nav > ul > li > a:hover {
    // width: 150px;
    color: $violet;
}

