// 슬라이드

.mainSlider > .sliderWrap {
    width: 1280px;
    height: 360px;
    overflow: hidden;
} 

.mainSlider > .sliderWrap > .sliderImg {
    width: 3840px;
    animation: sliderLoop 6s infinite ;
}
.mainSlider > .sliderWrap > .sliderImg > img {
    width: 1280px;
}

@keyframes sliderLoop {
    0% {
        transform: translateX(0px);
    }
    50% {
        transform: translateX(-1280px);
    }
    100% {
        transform: translateX(-2560px);
    }
}

// 기온 추천 코너
.temperatureRecommendation > .recommendationWrap{
    width: 1280px;
    height: 300px;
    @include flexRow();
}
.temperatureRecommendation h2 {
    width: 225px;
    color: $violet;
    font-size: 24px;
    margin: 150px auto;
} 

.temperatureRecommendation .recommendationPlace {
    width: 520px;
    margin: 0 auto;
    @include flexRow();
}
.temperatureRecommendation button {
    width: 150px;
    height: 45px;
    font-size: bold;
    border: 1px solid $violet;
    border-radius: 20px;
    background-color: $violet;
    color: #ffffff;
    margin-top: 200px;
}

// 베스트 상품 코너
.mainProduct > .productWrap {
    width: 1280px;
    height: 500px;
}

.mainProduct > .productWrap > h2 {
    width: 200px;
    height: 30px;
    margin: 55px auto;
}

.mainProduct > .productWrap > .productPlace {
    @include bestProductList();
}

// 메인 페이지 상품카드
.productWrap {
    width: 250px;
    height: 310px;
    font-size: 18px;
    text-align: center;
}

.productWrap > img {
    width: 200px;
    height: 180px;
}

.productWrap > .productName {
    width: 125px;
    height: 30px;
    margin: 20px auto;
}

.productWrap > .productPrice {
    width: 115px;
    height: 15px;
    margin: 20px auto;
}




// footer
.mainFooter {
    width: 100vw;
    height: 150px;
    background-color: #F5F3F3;
}
.mainFooter > .footerWrap {
    width: 1280px;
}



// coverStore 
.storeProduct {
    width: 1280px;
    height: 1200px;
    margin: 0 auto;
}

.storeProduct > h2{
    width: 90px;
    height: 30px;
    margin: 0 auto;
}

.storeProduct .basicWrap {
    width: 980px;
    height: 1000px;
    margin: 70px auto;
}

.storeProduct .basicPlace {
    @include productList();
}

.storeProduct .basicPlace > .productWrap {
    margin-top: 25px;
}
.storeProduct .basicPlace > .productWrap > p {
    width: 160px;
}

// 상품 상세페이지 
.detailWrap {
    width: 1025px;
    height: 380px;
    margin: 25px auto;
    @include flexRow();
}
.detailWrap > .detailImg {
    width: 380px;
    height: 345px;
}

.detailWrap > .detailInformation {
    width: 250px;
    height: 380px;
    font-size: 20px;
    margin: 20px 0 0 115px;
    @include flexColumn();
    justify-content: space-around;
}

.detailWrap > .detailInformation > .detailTitle {
    width: 165px;
    height: 30px;
    font-weight: bold;
    font-size: 24px;
}

.detailWrap > .detailInformation > .detailPrice {
    margin-top: 45px;
}

.detailWrap .detailBtn {
    width: 450px;
    height: 80px;
    @include flexRow();
    justify-content: space-between;
}

.detailWrap .detailBtn > button{
    width: 210px;
    height: 65px;
    background-color: $violet;
    border: 1px solid $violet;
    color: #ffffff;
    font-size: 24px;
}

.detailWrap .detailBtn > button:hover {
    color: $violet;
    background-color: #ffffff;
}
