* {
  padding: 0;
  margin: 0;
}

ul, li {
  list-style: none;
}

a {
  color: black;
  text-decoration: none;
}

.wrap {
  width: 1280px;
  margin: 20px auto;
}

.login {
  width: 1280px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.login > a {
  margin-right: 10px;
}

.login > a:hover {
  color: #7469F0;
}

.logo {
  margin: 15px 0 0 25px;
  width: 150px;
  display: inline-block;
}

.logo > h1 {
  width: 150px;
  font-size: 30px;
  color: #7469F0;
  font-weight: bold;
}

.nav {
  margin: 15px 0 35px 110px;
  width: 800px;
  height: 25px;
  display: inline-block;
}

.nav > ul {
  width: 800px;
  display: flex;
  flex-direction: row;
  text-align: center;
  font-size: 20px;
}

.nav > ul > li {
  width: 180px;
  margin-left: 50px;
}

.nav > ul > li > a:hover {
  color: #7469F0;
}

.mainSlider > .sliderWrap {
  width: 1280px;
  height: 360px;
  overflow: hidden;
}

.mainSlider > .sliderWrap > .sliderImg {
  width: 3840px;
  animation: sliderLoop 6s infinite;
}

.mainSlider > .sliderWrap > .sliderImg > img {
  width: 1280px;
}

@keyframes sliderLoop {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(-1280px);
  }
  100% {
    transform: translateX(-2560px);
  }
}
.temperatureRecommendation > .recommendationWrap {
  width: 1280px;
  height: 300px;
  display: flex;
  flex-direction: row;
}

.temperatureRecommendation h2 {
  width: 225px;
  color: #7469F0;
  font-size: 24px;
  margin: 150px auto;
}

.temperatureRecommendation .recommendationPlace {
  width: 520px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
}

.temperatureRecommendation button {
  width: 150px;
  height: 45px;
  font-size: bold;
  border: 1px solid #7469F0;
  border-radius: 20px;
  background-color: #7469F0;
  color: #ffffff;
  margin-top: 200px;
}

.mainProduct > .productWrap {
  width: 1280px;
  height: 500px;
}

.mainProduct > .productWrap > h2 {
  width: 200px;
  height: 30px;
  margin: 55px auto;
}

.mainProduct > .productWrap > .productPlace {
  margin: 70px auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-content: space-around;
}

.productWrap {
  width: 250px;
  height: 310px;
  font-size: 18px;
  text-align: center;
}

.productWrap > img {
  width: 200px;
  height: 180px;
}

.productWrap > .productName {
  width: 125px;
  height: 30px;
  margin: 20px auto;
}

.productWrap > .productPrice {
  width: 115px;
  height: 15px;
  margin: 20px auto;
}

.mainFooter {
  width: 100vw;
  height: 150px;
  background-color: #F5F3F3;
}

.mainFooter > .footerWrap {
  width: 1280px;
}

.storeProduct {
  width: 1280px;
  height: 1200px;
  margin: 0 auto;
}

.storeProduct > h2 {
  width: 90px;
  height: 30px;
  margin: 0 auto;
}

.storeProduct .basicWrap {
  width: 980px;
  height: 1000px;
  margin: 70px auto;
}

.storeProduct .basicPlace {
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-basis: 250px;
}

.storeProduct .basicPlace > .productWrap {
  margin-top: 25px;
}

.storeProduct .basicPlace > .productWrap > p {
  width: 160px;
}

.detailWrap {
  width: 1025px;
  height: 380px;
  margin: 25px auto;
  display: flex;
  flex-direction: row;
}

.detailWrap > .detailImg {
  width: 380px;
  height: 345px;
}

.detailWrap > .detailInformation {
  width: 250px;
  height: 380px;
  font-size: 20px;
  margin: 20px 0 0 115px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.detailWrap > .detailInformation > .detailTitle {
  width: 165px;
  height: 30px;
  font-weight: bold;
  font-size: 24px;
}

.detailWrap > .detailInformation > .detailPrice {
  margin-top: 45px;
}

.detailWrap .detailBtn {
  width: 450px;
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.detailWrap .detailBtn > button {
  width: 210px;
  height: 65px;
  background-color: #7469F0;
  border: 1px solid #7469F0;
  color: #ffffff;
  font-size: 24px;
}

.detailWrap .detailBtn > button:hover {
  color: #7469F0;
  background-color: #ffffff;
}/*# sourceMappingURL=App.css.map */